.food {
  .plate {
    position: relative;
    z-index: 1;
  }

  .item-container {
    z-index: 2;
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    .item {
      position: relative;
      //   left: -100%;
      border: 4px solid green;
    }
  }
}
