}

.event .event-description:hover {
  background-color: #edf7fa; /* For browsers that do not support gradients */
  background-image: -webkit-radial-gradient(
    white 10%,
    #edf7fa 90%
  );
  background-image: radial-gradient(
    white 10%,
    #edf7fa 90%
  ); /* Standard syntax (must be last) */
  border-radius: 50%;
}

.event .animate-water {
  width: 45%;
  margin-left: 30%;
  margin-top: 30px;
}

.event .text {
  font-size: 40px;
}

.cup1 {
  fill: #edf3ff;
}
.cup1,
.cup2 {
  stroke: #919191;
  stroke-miterlimit: 10;
  stroke-width: 5;
}
.cup2 {
  fill: none;
  stroke-width: 5;
}

#cup .content {
  fill: lightblue;
  /* transform: translate(0, -100%); */
}

#bottle .cup-animate {
  animation: bottle 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  -webkit-animation: bottle 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
}

#power .cup-animate {
  animation: power 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  -webkit-animation: power 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
}

#paper .cup-animate {
  animation: paper 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  -webkit-animation: paper 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
}

#burger .cup-animate {
  animation: burger 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  -webkit-animation: burger 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
}

.text-animate {
  animation: text 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  -webkit-animation: text 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
}

@-webkit-keyframes bottle {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 250px);
            transform: translate(0, 250px);
  }
}
@keyframes bottle {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 250px);
            transform: translate(0, 250px);
  }
}

@-webkit-keyframes power {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 1900px);
            transform: translate(0, 1900px);
  }
}
@keyframes power {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 190px);
            transform: translate(0, 190px);
  }
}

@-webkit-keyframes paper {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 250px);
            transform: translate(0, 250px);
  }
}
@keyframes paper {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 250px);
            transform: translate(0, 250px);
  }
}

@-webkit-keyframes burger {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 30px);
            transform: translate(0, 30px);
  }
}
@keyframes burger {
  from {
    -webkit-transform: translate(0, 299.07px);
            transform: translate(0, 299.07px);
  }
  to {
    -webkit-transform: translate(0, 30px);
            transform: translate(0, 30px);
  }
}

@-webkit-keyframes text {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes text {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#change .title {
  margin-bottom: 50px;
}
#change .title .badge {
  font-size: 2.5em;
  background-color: #ecf6f5;
  color: black;
}

#change .cls-1 {
  fill: #9dd5e8;
}
#change .cls-2 {
  fill: #d6e5e3;
}
#change .cls-3 {
  stroke: #9dc8cf;
  stroke-miterlimit: 10;
  stroke-width: 3px;
  fill: transparent;
}
#change .cls-4 {
  fill: transparent;
}

#change .active-step {
  stroke: #7087ce;
  stroke-miterlimit: 10;
  stroke-width: 3px;
}

#change .wrong,
#change .right {
  font-size: 1em;
  text-align: left;
  color: #4d4d4d;
}

#change .wrong::before {
  content: ' ';
  background-image: url(/earth/static/media/wrong.ed3e6fd3.png);
  background-size: 1em 1em;
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.5em;
}

#change .right::before {
  content: ' ';
  background-image: url(/earth/static/media/right.b11d445b.png);
  background-size: 1em 1em;
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.5em;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  background-color: #3f5577;
}

.image-container:hover .overlay {
  opacity: 1;
}

.image-container .text {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

